* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container {
    border-radius: 2rem;
    width: 80%;
    margin: 4rem auto;
    background-image: linear-gradient(red, green);
    padding: 1.5em;
}

@media screen and (min-width:700px) {
    .container {
        border-radius: 2rem;
        width: 20%;
        margin: 6rem auto;
        background-image: linear-gradient(red, green);
        padding: 2em;
    }
}

header {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
}

header input {
    border-radius: 1em;
    height: 2rem;
    padding: 1rem;
    width: 80%;
    border: none;
    font-size: 1rem;
}

header img {
    border-radius: 50%;
    position: relative;
    top: 5px;
    width: 13%;
    height: 28px;
}

header img:hover {
    transform: scale(130%, 130%);
}

main {
    display: flex;
    flex-flow: column wrap;
    padding: 2rem;
    align-items: center;
}

.cloud {
    width: 45%;
    height: 50%;
}

.cloud:hover {
    transform: scale(100%, 100%);
}

.degree {
    margin-top: 1rem;
    font-size: 3em;
    color: white;
}

.city {
    margin-top: 1rem;
    font-size: 2em;
    color: white;
}

footer {
    display: flex;
    justify-content: space-between;
    position: relative;
    left: 19px;
}

.humidity {
    width: 50%;
    height: 40px;
}

.wind {
    width: 50%;
    height: 35px;
}

.percentage {
    font-size: 1rem;
    position: relative;
    left: -10px;
}

.humi {
    font-weight: bold;
    font-size: 1rem;
    position: relative;
    left: -13px;
}

.km {
    font-size: 1.2rem;
    position: relative;
    left: -5px;
}

.windspeed {
    font-size: 1rem;
    position: relative;
    left: -8px;
}